export default function animacao() {
    const characterImage = document.getElementById('character-image');
    
    // 游꼣 Array com as URLs das imagens na ordem de anima칞칚o
    const characterImages = [
        '../img/pizzito.png', // pizzitoNormal
        '../img/pizzito-piscando.png', // Exemplo: Adicione a imagem de piscar
        '../img/pizzito-aguardando.png' // pizzitoAguardando
    ];

    let imageIndex = 0; // Inicia no 칤ndice 0 (primeira imagem)
    const intervalTime = 2000; // 200 milissegundos

    // 游뛀 Vari치vel para controlar se o bot est치 digitando (se necess치rio)
    // Se o bot estiver digitando, a anima칞칚o de troca pode ser pausada.
    let isBotTyping = false; 

    function animateCharacter() {
        if (isBotTyping) return; // Se o bot estiver digitando, para a anima칞칚o.

        // Define a URL da imagem atual
        characterImage.src = characterImages[imageIndex];

        // Incrementa o 칤ndice para a pr칩xima imagem
        imageIndex++;

        // Se o 칤ndice for igual ou maior que o n칰mero total de imagens, 
        // ele volta para 0, criando um loop
        if (imageIndex >= characterImages.length) {
            imageIndex = 0;
        }
    }

    // Inicia a anima칞칚o imediatamente ou ap칩s um pequeno delay inicial
    // Se voc칡 quer que comece a cada 200ms logo de cara, chame o setInterval direto.
    // Usei setTimeout com 50ms apenas para garantir que o DOM est치 pronto, se necess치rio.
    setTimeout(() => {
        // Define o intervalo para chamar a fun칞칚o de anima칞칚o a cada 200ms
        setInterval(animateCharacter, intervalTime); 
    }, 50); // Delay inicial bem pequeno
}